@import "../../../styles/base/base";
@import "../../../styles/mixins/mixins";

$_disabled-color: #cccccc;

.button {
  cursor: pointer;
  background: $color-monarch;
  // background: $color-shiraz;
  color: $color-white;
  padding: 8px 20px;
  font-size: 1.2rem;
  letter-spacing: 1px;
  border-radius: 5px;
  border: 1px solid $color-monarch;
  @include mainTransition;

  &:hover {
    border-color: $color-crown-of-thorns;
    background: $color-crown-of-thorns;
  }

  &:active {
    outline: none;
    background: $transparent;
  }

  &__disabled {
    color: $_disabled-color;
    background: $color-manatee;
    border-color: $color-manatee;

    &:hover,
    &:active {
      background: $color-manatee;
      border-color: $color-manatee;
    }
  }
}
